<template>
  <div class="attachment-container" :class="{ isSmall: size === 'small' }">
    <el-form-item label="附件:" v-if="size === 'normal'">
      <el-upload
        action="/attachment"
        :show-file-list="false"
        :before-upload="beforeUpload"
        :on-success="handleSuccess"
        :on-error="handleError"
        :on-progress="handleProgress"
        :file-list="fileList">
        <el-button size="small" icon="el-icon-upload2" :loading="isUploading">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">支持格式：.rar，.zip，.doc，.docx，.pdf，.xls，.xlsx，.txt，.jpg，.jpeg，.png，单个文件不能超过20M</div>
      </el-upload>
    </el-form-item>

    <el-upload
      v-else
      action="/attachment"
      :show-file-list="false"
      :before-upload="beforeUpload"
      :on-success="handleSuccess"
      :on-error="handleError"
      :on-progress="handleProgress"
      :file-list="fileList">
      <el-button size="small" icon="el-icon-upload2" :loading="isUploading">选择附件</el-button>
      <div slot="tip" class="el-upload__tip">支持格式：.rar，.zip，.doc，.docx，.pdf，.xls，.xlsx，.txt，.jpg，.jpeg，.png，单个文件不能超过20M</div>
    </el-upload>

    <el-table
      :data="fileList"
      :show-header="size === 'normal'"
      size="mini"
      empty-text="暂无附件"
      style="width: 100%">
      <el-table-column label="序号" type="index" align="center"></el-table-column>
      <el-table-column label="附件名称" prop="name" align="center">
        <template slot-scope="scope">
          <span v-if="size === 'small'">{{ scope.row.name | fmtName(scope.row, size) }}</span>
        </template>
      </el-table-column>

      <template v-if="size === 'normal'">
        <el-table-column label="附件格式" prop="format" align="center"></el-table-column>
        <el-table-column label="文件大小" prop="size" align="center">
          <template slot-scope="scope">{{ scope.row.size | fmtSize }}</template>
        </el-table-column>
        <el-table-column label="上传时间" prop="createTime" align="center">
          <template slot-scope="scope">{{ scope.row.createTime | fmtDateTime }}</template>
        </el-table-column>
        <el-table-column label="上传人" prop="creater" align="center"></el-table-column>
      </template>

      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-link icon="el-icon-download" @click="onDownLoad(scope.row.url)">下载</el-link>
          <el-popconfirm title="是否确认删除？" confirmButtonType="danger" @onConfirm="onRemoveAttachment(scope.row.fid)" style="margin-left: 10px;">
            <el-link slot="reference" icon="el-icon-remove-outline" type="danger">删除</el-link>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'attachment',
  props: {
    list: Array,
    size: {
      type: String,
      default: 'normal'
    }
  },
  data () {
    return {
      fileList: [],
      isUploading: false
    }
  },
  watch: {
    list: {
      handler: function (val) {
        this.fileList = val
      },
      immediate: true
    }
  },
  filters: {
    fmtSize (val) {
      if (parseInt(val / 1024) === 0) {
        return val + 'B'
      }
      if (parseInt(val / 1024 / 1024) === 0) {
        return parseFloat(val / 1024).toFixed(2) + 'KB'
      }
      if (parseInt(val / 1024 / 1024 / 1024) === 0) {
        return parseFloat(val / 1024 / 1024).toFixed(2) + 'MB'
      }
    },

    fmtName (val, row, size) {
      return val && size === 'small' && row.format ? (val + '.' + row.format.toLowerCase()) : val
    }
  },
  methods: {
    // 上传前的验证
    beforeUpload (file) {
      const isMatchType = /[\s\S]*(.rar|.zip|.doc|.docx|.pdf|.xls|.xlsx|.txt|.jpg|.jpeg|.png)$/.test(file.name)
      const isLt20M = file.size / 1024 / 1024 < 20

      if (!isMatchType) {
        this.$message.error('文件格式只能是.rar，.zip，.doc，.docx，.pdf，.xls，.xlsx，.txt，.jpg，.jpeg，.png的!')
      }
      if (!isLt20M) {
        this.$message.error('单个文件不能超过20M!')
      }
      if (isMatchType && isLt20M) {
        this.isUploading = true
      }
      return isMatchType && isLt20M
    },

    handleProgress (event, file, fileList) {
      // console.log('handleProgress')
      // console.log(event, file, fileList)
    },

    // 上传成功的处理
    handleSuccess (response, file, fileList) {
      this.isUploading = false
      this.fileList.push(response.body)
      this.$emit('update:list', this.fileList)
    },

    // 上传失败的处理
    handleError (err, file, fileList) {
      // console.log(err, file, fileList)
      this.$message.error('上传失败：' + err)
      this.isUploading = false
    },

    // 下载附件
    onDownLoad (url) {
      // todo:下载文件
    },

    // 删除附件
    onRemoveAttachment (id) {
      const idx = this.fileList.findIndex(f => f.fid === id)
      idx >= 0 && this.fileList.splice(idx, 1)
    }
  }
}
</script>

<style lang="scss" scoped>
.attachment-container {
  margin: 10px 0;

  &.isSmall {
    margin: 0;
  }

  .el-upload__tip {
    line-height: 20px;
  }

  .el-link {
    font-size: 12px;
    font-weight: normal;
  }
}
</style>
